import React, { Component } from "react";
import { Icon, Input, Button } from "react-onsenui";
import "./ShortAnswerTask.css";
import FeedbackMsg from "../FeedbackMsg/FeedbackMsg";
import TaskDescription from "../TaskDescription/TaskDescription";

class ShortAnswerTask extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isCorrect: this.props.isDone,
      answer: ""
    };

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    if (this.state.isCorrect) return;

    const activity = this.props.activity;
    const answers = activity.answers.map(item => item.toLowerCase());

    this.setState({ isCorrect: answers.includes(this.state.answer.toLowerCase().trim()) });
  }

  componentDidUpdate() {
    if (this.state.isCorrect) {
      this.props.setActivityDone(this.props.activity._id);
    }
  }

  render() {
    const activity = this.props.activity;

    return (
      <div className="ShortAnswerTask-wrapper">
        {this.state.isCorrect && <FeedbackMsg type="correct" />}
        {this.state.isCorrect === false && <FeedbackMsg type="wrong" />}
        <TaskDescription
          name={activity.activityName}
          description={activity.activityDescription}
        />
        <div className="ShortAnswerTask-answer_wrapper">
          <div>
            <Input
              className="ShortAnswerTask-input"
              value={this.state.isCorrect ? activity.answers[0] : this.state.answer}
              float
              onChange={event => {
                this.setState({ answer: event.target.value });
              }}
              type="text"
              placeholder={this.state.isCorrect ? "The answer" : "Your answer"}
              readOnly={this.state.isCorrect ? true : false}
              required
            />
          </div>
          <Button
            className="ShortAnswerTask-button"
            modifier='cta'
            ripple
            onClick={this.handleClick}
            disabled={this.state.isCorrect ? true : false}
          >
            Submit
          </Button>
        </div>
      </div>
    );
  }
}

export default ShortAnswerTask;
