import React, { Component } from 'react';
import * as ons from 'onsenui';
import { Page, Button, Icon } from 'react-onsenui';
import QrReader from 'react-qr-reader';
import ActivityListPage from '../ActivityListPage/ActivityListPage';
import NavBar from '../NavBar/NavBar';
import './LocationPage.css';

class LocationPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      result: null,
      displayCamera: false
    }

    this.getMap = this.getMap.bind(this);
    this.handleScan = this.handleScan.bind(this);
    this.handleError = this.handleError.bind(this);
    this.displayCamera = this.displayCamera.bind(this);
    this.hideCamera = this.hideCamera.bind(this);
    this.pushPage = this.pushPage.bind(this);
    this.resetState = this.resetState.bind(this);
    this.renderDescription = this.renderDescription.bind(this);
    this.renderCamera = this.renderCamera.bind(this);
    this.getDistance = this.getDistance.bind(this);
    this.isWithinLocation = this.isWithinLocation.bind(this);
  }

  handleScan = data => {
    const location = this.props.location;
    if (data) {
      if (data === location.title) {
        this.setState({
          result: data
        });
        this.setState({
          displayCamera: false
        });
        this.pushPage(this.props.navigator);
      } else {
        this.setState({
          displayCamera: false
        });
        ons.notification.alert(`Sorry, looks like you are not at ${location.title}`);
      }
    }
  }

  handleError = err => {
    console.error(err)
  }

  displayCamera() {
    this.setState({
      displayCamera: true
    });
  }

  hideCamera() {
    this.setState({
      displayCamera: false
    });
  }

  getDistance(lat1, lon1, lat2, lon2) {
    function deg2rad(deg) {
      return deg * (Math.PI/180)
    }
    
    var R = 6371; // Radius of the earth in km
    var dLat = deg2rad(lat2-lat1);  // deg2rad below
    var dLon = deg2rad(lon2-lon1); 
    var a = 
      Math.sin(dLat/2) * Math.sin(dLat/2) +
      Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) * 
      Math.sin(dLon/2) * Math.sin(dLon/2)
      ; 
    var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a)); 
    var d = R * c; // Distance in km
    return d;
  }

  isWithinLocation() {
    const location = this.props.location;
    //Check if Geolocation is supported 
    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((pos) => {
          console.log(location.coords.latitude);
          console.log(location.coords.longitude);
          const distance = this.getDistance(
              pos.coords.latitude,
              pos.coords.longitude,
              location.coords.latitude,
              location.coords.longitude
            );
          console.log(distance);
          if (distance <= 0.3) {
            this.pushPage(this.props.navigator);
          } else {
            ons.notification.alert(`Sorry, looks like you are not at ${location.title}. Maybe get closer?`);
          }
        }, null, {enableHighAccuracy: true});
    } else { 
      //If not, display a message to the user
      console.log("Geolocation is not supported by your browser");
    }
  }

  getMap(locationName) {
    window.open(`https://maps.google.com/?q=${locationName.replace(' ', '+')}`);
  }

  resetState() {
    this.setState({
      result: null,
      displayCamera: false
    });
  }

  pushPage(navigator) {
    navigator.pushPage({
      component: ActivityListPage,
      props: {
        result: this.state.result,
        resetState: this.resetState,
        locationId: this.props.location._id,
        token: this.props.token,
        setLocationDone: this.props.setLocationDone,
        key: `ACTIVITY_LIST_PAGE_${Date.now()}`,
      },
    });
  }

  renderDescription() {
    const location = this.props.location;
    return (
      <div>
        <div className="LocationPage-img-wrapper">
          <img src={location.image} alt={location.title} />
        </div>
        <div className="title center LocationPage-title">
          {location.title}
        </div>
        <div className="content LocationPage-info_wrapper">
          <div>
            <div>
              <p className="LocationPage-address_lbl">Location</p>
              <p className="LocationPage-address">{location.address}</p>
            </div>
            <div>
              <p className="LocationPage-hours_lbl">Hours</p>
              <p className="LocationPage-hours">{location.hours}</p>
            </div>
            <div>
              <p className="LocationPage-description_lbl">Description</p>
              <p className="LocationPage-hours">{location.description}</p>
            </div>
          </div>
        </div>
        <section style={{ margin: '16px' }}>
          <div style={{ margin: '6px' }}>
            <Button onClick={location.type === "external" ? this.isWithinLocation : this.displayCamera} modifier='large'>I am here</Button>
          </div>
          {this.props.location.type === "external" && 
            <div style={{ margin: '6px' }}>
              <Button onClick={() => this.getMap(location.address)} modifier='large outline'>Get directions</Button>
            </div>
          }
        </section>
      </div>
    );
  }

  renderCamera() {
    return (
      <div>
        <div className='LocationPage-QR_description'>
          <p><Icon icon='qrcode' size={68} /></p>
          <p>Scan QR code to open a list of activities</p>
        </div>
        <QrReader
          delay={1000}
          onError={this.handleError}
          onScan={this.handleScan}
          style={{ width: '100%' }}
        />
        <Button onClick={this.hideCamera} modifier='large' className='LocationPage-QR_cancel_btn'>Cancel</Button>
      </div>
    );
  }

  render() { 
    return (
      <Page renderToolbar={() => 
        <NavBar
          title={this.props.location.title}
          navigator={this.props.navigator}
          backButton
        />
      }>
        {this.state.displayCamera ? this.renderCamera() : this.renderDescription()}
      </Page>
    );
  }
}

export default LocationPage;