import React, { Component } from "react";
import * as ons from 'onsenui';
import { Icon, Card } from "react-onsenui";
import QrReader from 'react-qr-reader';
import "./ScanQRCodeTask.css";
import FeedbackMsg from "../FeedbackMsg/FeedbackMsg";
import TaskDescription from "../TaskDescription/TaskDescription";

class ScanQRCodeTask extends Component {
  constructor(props) {
    super(props);

    this.state = {
      displayCamera: null,
      isDone: this.props.isDone
    };

    this.displayCamera = this.displayCamera.bind(this);
  }

  displayCamera() {
    this.setState({
      displayCamera: true
    });
  }

  handleScan = data => {
    const activity = this.props.activity;
    if (data) {
      if (data === activity.content) {
        this.setState({
          isDone: true,
          displayCamera: false,
        });
      } else {
        ons.notification.alert({
          message: 'Sorry, seems like you scanned a wrong QR code!',
          title: "Ooops"
        });
      }
    }
  }

  handleError = err => {
    console.error(err)
  }

  componentDidUpdate() {
    if (this.state.isDone) {
      this.props.setActivityDone(this.props.activity._id);
    }
  }

  render() {
    const activity = this.props.activity;

    return (
      <div className="ScanQRCodeTask-wrapper">
        {this.state.isDone && <FeedbackMsg type="correct" />}
        <TaskDescription
          name={activity.activityName}
          description={activity.activityDescription}
        />
        {!this.state.isDone ? (
          <div>
            <QrReader
              delay={1000}
              onError={this.handleError}
              onScan={this.handleScan}
              style={{ width: '100%' }}
            />
          </div>
        ) : (
          <div className="ScanQRCodeTask-content">
            {activity.content}
          </div>
        )}
      </div>
    );
  }
}

export default ScanQRCodeTask;
