import React from "react";
import { Icon } from "react-onsenui";
import "./FeedbackMsg.css";

const FeedbackMsg = ({ type }) => {
  return (
    <div
      className={
        "FeedbackMsg " +
        (type === "correct" ? "FeedbackMsg-correct" : "FeedbackMsg-wrong")
      }
    >
      <Icon icon={type === "correct" ? "check" : "times"} />
      &nbsp;&nbsp;{type === "correct" ? "Completed!" : "Try again!"}
    </div>
  );
};

export default FeedbackMsg;
